/* eslint-disable prefer-const */
/* eslint-disable no-param-reassign */
/* eslint-disable indent */
function changeFontSize(mySize) {
    const elements = document.querySelectorAll('*'); // Select all elements
    elements.forEach((element) => {
        if (element.id === 'title') {
            element.style.fontSize = `${mySize + 5}px`;
        } else {
            element.style.fontSize = `${mySize}px`; // Change font size for each element
        }
    });
    document.getElementById('fontSize').innerText = `Font Size: ${mySize}px`;
}
let size = 20;
changeFontSize(size);

function listenForFontSize() {
    document.querySelector('#decreaseFontSize').addEventListener('click', () => {
        // eslint-disable-next-line no-unused-expressions
        ((size - 1 >= 12) ? changeFontSize(size -= 5) : changeFontSize(size));
    });
    document.querySelector('#increaseFontSize').addEventListener('click', () => {
        // eslint-disable-next-line no-unused-expressions
        ((size + 1 <= 40) ? changeFontSize(size += 5) : changeFontSize(size));
    });
}

listenForFontSize();